export type locStats = {
  location: {
    name: string;
    region: string;
    country: string;
    lat: number;
    lon: number;
    tz_id: string;
    localtime_epoch: number;
    localtime: string;
  };
  current: {
    last_updated_epoch: number;
    last_updated: string;
    temp_c: number;
    temp_f: number;
    is_day: number;
    condition: {
      text: string;
      icon: string;
      code: number;
    };
    wind_mph: number;
    wind_kph: number;
    wind_degree: number;
    wind_dir: string;
    pressure_mb: number;
    pressure_in: number;
    precip_mm: number;
    precip_in: number;
    humidity: number;
    cloud: number;
    feelslike_c: number;
    feelslike_f: number;
    vis_km: number;
    vis_miles: number;
    uv: number;
    gust_mph: number;
    gust_kph: number;
  };
};

export async function getWeatherData(loc?: string): Promise<locStats> {
  let urlStr: string;
  if (loc) {
    const searchStr = encodeURI(loc as string);
    urlStr = `https://weatherapi-com.p.rapidapi.com/current.json?q=${searchStr}`;
  } else {
    urlStr = "https://weatherapi-com.p.rapidapi.com/current.json?q=patna";
  }

  const response: Response = await fetch(urlStr, {
    method: "GET",
    headers: {
      "x-rapidapi-key": "3e54eccc67msh4a93ffd74125e4ep18034djsne94106419d2a",
      "x-rapidapi-host": "weatherapi-com.p.rapidapi.com",
    },
  });

  return response.json();
}
